const uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'redirect',
  // Redirect to /signedIn after sign in is successful. Alternatively you can provide a callbacks.signInSuccess function.
  signInSuccessUrl: '/page2',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    //firebase.auth.FacebookAuthProvider.PROVIDER_ID
  ]
};

let urls = ['https://www.youtube.com/watch?v=TAZYqXwW5lA',
              'https://vimeo.com/265363100',
              'https://vimeo.com/265363100'];

const x = Math.floor(Math.random() * urls.length);

function Topbar(props) {
  return (
    <div style={{backgroundColor: '#282c34', height:100, textAlign:'center' }}>
      <h2 style={{padding:40, color:'white', fontSize:34}}>{props.title}</h2>
    </div>
  );
}


class Page1 extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      account: '',
      balance: '',
      videoPlayed: false,
      gotLinks: false,
      active: false,
      apiResponse: "" 
    };

  }

  
  handleShow = () => this.setState({ active: true })
  handleHide = () => this.setState({ active: false })

  componentDidMount() {
    const values = queryString.parse(this.props.location.search)
    
      var campaignRef = firebase.database().ref('adCampaigns/' + values.adWalletAddress);
      
      if(values.adWalletAddress == null ){
        campaignRef = firebase.database().ref('adCampaigns/0x619e4bc22CB5079D154640c1ba2fCC936A12606e');
      }
      
      

      campaignRef.once('value').then(function(snapshot) {
        // The first promise succeeded. Save snapshot for later.
        if ( snapshot.val().link1 != ""){
          urls[0] = snapshot.val().link1;
        }
        if ( snapshot.val().link2 != ""){
          urls[1] = snapshot.val().link2;
        }
        if ( snapshot.val().link3 != ""){
          urls[2] = snapshot.val().link3;
        }
         console.log(snapshot.val().link1);
         console.log(snapshot.val().link2);
         console.log(snapshot.val().link3);
        
        
      }).then(() => {
        this.setState({ gotLinks: true })
      }, function(error) {
        // Something went wrong.
        console.error(error);
      });
/*
    const isTorus = sessionStorage.getItem('pageUsingTorus')

    if (isTorus) {
      web3Obj.initialize().then(() => {
        this.setStateInfo()
      })
    }
    */
  }



  callAPI() {
    const values2 = queryString.parse(this.props.location.search)

    let fetchStringWithWalletAddress = "https://us-central1-cryptoads-77142.cloudfunctions.net/payout2?ClientWalletAddress=" + this.state.account + "&AdWalletPublic=" + values2.adWalletAddress;

    fetch(fetchStringWithWalletAddress)
        .then(res => res.text())
        .then(res => this.setState({ apiResponse: res }))
        .then(res => console.log(this.state.apiResponse))
        .then(  () => {
          if(this.state.apiResponse === "Success"){
            this.props.history.push('/page2/')
          }
        });
  }

/*
  setStateInfo = () => {
    
    web3Obj.web3.eth.getAccounts().then(accounts => {
      this.setState({ account: accounts[0] })
      web3Obj.web3.eth.getBalance(accounts[0]).then(balance => {
        this.setState({ balance: balance })
        if(this.state.videoPlayed){
          this.callAPI();
        }
      })
    })
    
  }

  enableTorus = async () => {
    this.handleShow()
    try {
      await web3Obj.initialize()
      this.setStateInfo()
    } catch (error) {
      console.error(error)
    }
  }
  */

  loadUserWallet = async () => {
    
   await this.setState({ account: '0x830c5D312D507DdB066192d34dD6441737e127C8' });
   this.callAPI();
      
  }

  render() {

    const { active } = this.state

      if(!this.state.gotLinks ){
        return (
          <div >
            <Topbar title="Play Video to Earn $"/>
          </div>
        );
      }
      else if(!this.state.videoPlayed ){
        return (
          <div >
            <Topbar title="Play & Earn $"/>

              <ReactPlayer
                url={ urls[x] } 
                playing
                width='100%'
                onEnded={() => {
                    console.log('onEnded')
                    this.setState({
                      videoPlayed: true
                    });
                  }}
              className="Player"  
              />
            
            <div style={{backgroundColor: '#282c34', height:150, textAlign:'center' }}></div>
          </div>
        );
      }
      else{
        return (
          <div>
          <Dimmer.Dimmable as={Segment} blurring dimmed={active} style={{padding:0 }}>
          
            <Topbar title="Thanks!"/>
            <ReactPlayer
              url={ urls[x] } 
              width='100%'
              style={{pointeEvents: 'none'}}
            />
            <div style={{backgroundColor: '#282c34', height:100, textAlign:'center' }}>
              <h4>Claim $$</h4>
              <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()}/>
            </div>
             
          </Dimmer.Dimmable>
            
            
          <Dimmer active={this.state.active}>
            <Loader inverted>Loading</Loader>
          </Dimmer>

          </div>
        );
      }
  }
}

export default Page1

